const useStyles = makeStyles(() => ({
  root:{
    flexGrow: 1,
    display: 'flex',
    marginTop: 7,
    height: "50px",
  },
  icons:{
    color: "white"
  },
  containerGraphics:{
    marginTop: '40px'
  },
  containerTable:{
    marginTop: '40px'
  },
  section:{
    height: "100%",
  },
  categories:{
    marginTop: 7,
  },
  button:{
    flexGrow: 1,



  },
  details: {
    display: 'flex',
    flexDirection: 'column',
  },




}));

function generate(element) {
  return [0, 1, 2].map((value) =>
    cloneElement(element, {
      key: value,
    }),
  );
}
export default function Category(props){
    const {  register, handleSubmit, errors, control } = useForm();
    const [dense, setDense] = useState(false);
    const [secondary, setSecondary] = useState(false);


  const classes= useStyles();

  const redTheme = createMuiTheme({ palette: { 
    primary: {
        main: "#ab003c",
        contrastText: "#fff"
    } }})

    
  const handleNotification = (value) =>{

    if(value == "error"){
      toast.error(" Error!", {
        position: "bottom-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
    });
    }
    else{
      toast.success(" Saved!", {
        position: "bottom-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
    });
    }
  }
  const [showResults, setShowResults] = useState(true)

  const onSubmitNew = handleSubmit(data => {
    data.formType = 'formNew'
    data.TicketCategory = props.icon
    console.log(data)   
    fetch(`/api/removecategory/${props.url}`,{
    method: "POST",
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(data)
  }).then(response => response.json())
  .then(data => {
    handleNotification(data.response)
    setShowResults(false)
    props.onChange(data.response)
  }

  )
  });


  return(
    <form>
          {showResults && (
                <ListItem>
                  <ListItemAvatar>
                    <Avatar>
                      <ForumIcon />
                    </Avatar>
                  </ListItemAvatar>
                  <ListItemText
                    primary={props.name}
                    secondary={secondary ? 'Secondary text' : null}

                  />
                  <ListItemSecondaryAction>
                    <IconButton edge="end" aria-label="delete">
                      <DeleteRoundedIcon onClick={onSubmitNew}/>
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
            )}


    </form>





  );

}