import {React, useEffect, useState} from 'react';
import Grid from '@material-ui/core/Grid';
import {makeStyles} from '@material-ui/core/styles';
import "fontsource-roboto";
import '../assets/Dashboard.css'
import CardPlugin from '../components/CardsPlugins';
import VerifiedUserIcon from '@material-ui/icons/VerifiedUser';
import ImageSearchIcon from '@material-ui/icons/ImageSearch';
import ExposureIcon from '@material-ui/icons/Exposure';
import EmailIcon from '@material-ui/icons/Email';
import CardGiftcardIcon from '@material-ui/icons/CardGiftcard';
import { DisappearedLoading } from 'react-loadingg';





export default function Plugins(props){


  const useStyles = makeStyles(() => ({
    root:{
      flexGrow: 1
    },
    icons:{
      color: "white"
    },
    containerGraphics:{
      marginTop: '40px'
    },
    containerTable:{
      marginTop: '40px'
    },
    loading:{
      marginLeft: '200px'
    }
  
  
  
  
  }));

  const[data, setData] = useState([])
  const [loading, setLoading] = useState(true);

  useEffect(() => {

    const getData = async () =>{
      setLoading(true);
      const response = await fetch(`/api/dashboard/${props.url}/plugins`)
      const response_json = await response.json()
      setData(response_json)
      console.log(response_json)
      setLoading(false);

    }
    getData()
  }, [])

  const clases= useStyles();





  return(
    <div className={clases.root}>
      {loading && <DisappearedLoading size="large" speed={.5} color="#587dd5" style={{marginLeft: "50%", marginButtom: "50%", marginTop: "20%"}}/>}
      {!loading &&
      <Grid container spacing={3}>

        <Grid item xs={12} sm={4} md={4} lg={4} lg={4}>
          <CardPlugin icon={<VerifiedUserIcon className={clases.root}/>} title="Verification" Roles={props.Roles} formSelect={"setverify"} Channels={props.Channels} url={props.url} checked={parseInt(data["VerifyEnabled"])} title="Verfication" text="Keep your server secure!" color="#3D3A3A" font="white"/>
        </Grid>

        <Grid item xs={12} sm={4} md={4} lg={4} lg={4}>
          <CardPlugin icon={<ImageSearchIcon className={clases.root}/>} title="Showcase" url={props.url} formSelect={"setshowcase"} Channels={props.Channels} url={props.url} checked={parseInt(data["ShowcaseEnabled"])} title="Showcase" text="Make a only images channel!" color="#3D3A3A" font="white"/>
        </Grid>

        <Grid item xs={12} sm={4} md={4} lg={4} lg={4}>
          <CardPlugin icon={<ExposureIcon className={clases.root}/>} title="[BETA] Active/Inactive" url={props.url} formSelect={"setactiveinactive"} Categories={props.Categories} checked={parseInt(data["ActiveInactiveEnabled"])} title="Active/Inactive" text="Show to your members what channels are currently active" color="#3D3A3A" font="white"/>
        </Grid>





      </Grid>



      }
    </div>
  );

}
