import {React, useState} from "react";
import ReactDOM from "react-dom";
import { useForm, ErrorMessage, Controller } from "react-hook-form";
import {
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  FormHelperText,
  Button,
  Grid,
  TextField
} from "@material-ui/core";
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import SaveIcon from '@material-ui/icons/Save';
import {makeStyles, withStyles} from '@material-ui/core/styles';

export default function FormShowcase(props) {


  const greenTheme = createMuiTheme({ palette: { 
    primary: {
        main: "#00e676",
        contrastText: "#fff"
    } }})

    const useStyles = makeStyles((theme) =>({
        root:{
            margin: theme.spacing(9),
            textAlign: "center",
        },
        submit:{
            marginTop: 30
        },
        selectField:{
            margin: theme.spacing(1),
            width: 380,
            minWidth: 120,
            "& .MuiOutlinedInput-input": {
              color: "white"
            },
            "& .MuiInputLabel-root": {
              color: "white"
            },
            "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
              borderColor: "white"
            },
            "&:hover .MuiOutlinedInput-input": {
              color: "white"
            },
            "&:hover .MuiInputLabel-root": {
              color: "white"
            },
            "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
              borderColor: "white"
            },
            "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-input": {
              color: "white"
            },
            "& .MuiInputLabel-root.Mui-focused": {
              color: "white"
            },
            "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
              borderColor: "white"
            },
            '& label.Mui-focused': {
                color: 'white',
            },
            '& .MuiInput-underline:after': {
            borderBottomColor: 'green',
            },
            '& .MuiOutlinedInput-root': {
                '& fieldset': {
                borderColor: 'white',
                },
                '&:hover fieldset': {
                borderColor: 'white',
                },
                '&.Mui-focused fieldset': {
                borderColor: 'white',
                },
            }  
        }
}));



    const classes = useStyles();

    const {  register, handleSubmit, errors, control } = useForm();


    const onSubmitNew = handleSubmit(data => {
      data.formType = 'formNew'
      console.log(data)   
      fetch(`/api/setshowcase/${props.url}`,{
      method: "POST",
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data)
  }).then(response => response.json())
  .then(data => props.onChange(data.response)
  )
  });

    const menuOptionsChannels = [];
 


    const getChannels = () =>{
        for(const Channel in props.Channels){
            menuOptionsChannels.push(<MenuItem key={Channel} value={String(Channel)}>{String(props.Channels[String(Channel)])}</MenuItem>);
        }
    }


    getChannels();

    return (
        <Grid>
        <div className={classes.root}>
        <form>
        <div>
            
        <Grid item xs={12}>
            <FormControl className={classes.selectField}
                style={{ minWidth: 300 }}
                error={Boolean(errors.channel)}
                >
                <InputLabel id="channel">
                    Channel of showcase
                </InputLabel>
                <Controller 
                    as={
                        <Select>
                            {menuOptionsChannels}
                        </Select>
                    }
                    name="channel"
                    rules={{ required: "This is required" }}
                    control={control}
                    defaultValue=""
                    color="secondary"
                    />
                <FormHelperText>
                    {errors.channel && errors.channel.message}
                </FormHelperText>
            </FormControl>
        </Grid>
        </div>
        <Grid item xs={12}>
            <ThemeProvider theme={greenTheme}>
                <Button
                    startIcon={<SaveIcon />}
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                    onClick={onSubmitNew}
                    >
                    Save
                </Button>
            </ThemeProvider>
        </Grid>
        </form>
        </div>

        </Grid>
        
    );
}