import { React, useCallback, useEffect, useState } from 'react';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import NavBar from '../components/NavBar';
import ServerSelector from '../components/ServerSelector';
import Grid from '@material-ui/core/Grid';
import PeopleAltIcon from '@material-ui/icons/PeopleAlt';
import {makeStyles} from '@material-ui/core/styles';
import { DisappearedLoading } from 'react-loadingg';
import { Scrollbar } from "react-scrollbars-custom";

const darkTheme = createMuiTheme({
    palette: {
      type: 'dark',
    },
    });

const UseStyles = makeStyles((theme) => ({
    root:{
        flexGrow: 1
    },
    icons:{
        color: "white"
    },
    containerGraphics:{
        marginTop: '200px'
    },
    containerTable:{
        marginTop: '40px'
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(24),
      },
    
    
    
    
    }));

    

export  default function SelectServer(){
    const clases= UseStyles();


    const [data, setData] = useState({})
    const [loading, setLoading] = useState(true);


    const tifOptions = [];


    useEffect(() => {
        setLoading(true);
        const getData = async () =>{
            setLoading(true);
            const response = await fetch(`/api/dashboard/`)
            const response_json = await response.json()
            if(response_json.type == "not logged"){
                window.location.href = "/api/login/";

            }
            else{
            setData(response_json)
            console.log(response_json)
            setLoading(false);
            }
          }
          getData()


        
    }, [])


    const processData = () => {
        const test = {"680843857591664879":{"Avatar":"https://www.online-tech-tips.com/wp-content/uploads/2019/09/discord.jpg","Name":"ModGaming"},"743211086458650684":{"Avatar":"https://cdn.discordapp.com/icons/743211086458650684/5e48bf70aee6bb57bf90b3ede7cc78dc.png","Name":"SpartBot Support"}};
        for(const te in data["mutual"]){
            tifOptions.push(<ServerSelector icon={<PeopleAltIcon className={clases.root}/>} linkto={"/dashboard/" + te + "/"} text="Dashboard" image={data["mutual"][te]["Avatar"]} title={data["mutual"][te]["Name"]} serverid={te} description="9438 Users" buttonVariant="contained" backgroundColor="#3D3A3A" font="white"/>)
        }
        for(const te in data["unmutual"]){
            tifOptions.push(<ServerSelector icon={<PeopleAltIcon className={clases.root}/>} linkto={false} externallinkto={"https://discord.com/oauth2/authorize?client_id=737002593510883358&scope=bot&permissions=268958800"} text="Invite" image={data["unmutual"][te]["Avatar"]} title={data["unmutual"][te]["Name"]} serverid={te} description="9438 Users" buttonVariant="outlined" backgroundColor="#3D3A3A" font="white"/>)
        }
    }  
    processData()

    console.log(tifOptions)
        return(
        <ThemeProvider theme={darkTheme}>
        <Scrollbar disableTracksWidthCompensation   style={{height: 970}}>

        <CssBaseline/>

        <main className={clases.content}>
        
        <div className={clases.root}>
        {loading && <DisappearedLoading size="large" speed={.5} color="#587dd5" style={{marginLeft: "50%", marginButtom: "50%", marginTop: "20%"}}/>}
        {!loading &&
        <Grid container spacing={4}>
            <Grid item xs={24} sm={24} md={24} lg={12}>
                {tifOptions}
            </Grid>
        </Grid>
        }
        </div>
        </main>
        </Scrollbar>
        </ThemeProvider>
        
    );
    
}
