import { React, useCallback, useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Content from '../components/Dashboard';
import {BrowserRouter as Router, Switch, Route, Link} from 'react-router-dom'
import Plugins from '../components/Plugins';
import NavBar from '../components/NavBar';
import Tickets from '../components/Tickets';
import InsertChartIcon from '@material-ui/icons/InsertChart';
import TuneIcon from '@material-ui/icons/Tune';


const drawerWidth = 200;

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
  },
  drawer: {
    type: 'dark',
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerContainer: {
    overflow: 'auto',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(7),
  },
    menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));





export default function DrawerNav(props) {

  const[message, setMessage] = useState([])


  const test = {"Channels":14,"GuildName":"SpartBot Support","Members":11}




  const darkTheme = createMuiTheme({
  palette: {
    type: 'dark',
  },
  });

  const navStyle = {
    color: "white",
    textDecoration: 'none'



  };

  const classes = useStyles();
  const [auth, setAuth] = useState(true);
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const { history } = props;
  const itemsList = [
    {
      text: "Dashboard",
      icon: <InsertChartIcon />,
      onClick: () => history.push("/dashboard/" + props.match.params.id + "/")
    },
    {
      text: "Plugins",
      icon: <TuneIcon />,
      onClick: () => history.push(`/dashboard/${props.match.params.id}/plugins/` )
    },
    {
      text: "Tickets",
      icon: <MailIcon />,
      onClick: () => history.push(`/dashboard/${props.match.params.id}/tickets/` )
    }
  ];
  const[data, setData] = useState([])
  const [loading, setLoading] = useState(true);

  useEffect(() => {

    const getData = async () =>{
      setLoading(true);
      const response = await fetch(`/api/dashboard/${props.match.params.id}/`)
      const response_json = await response.json()
      if(response_json.type == "not logged"){
        window.location.href = "/api/login/";
      }
      else if(response_json.type == "no permissions"){
        window.location.href = "/"
      }
      else{
        setData(response_json)
        console.log(response_json)
      }
      setLoading(false)
  
    }
    getData()
  }, [props.match.params.id])


  
  return (

    <ThemeProvider theme={darkTheme}>
    <div className={classes.root}>
      <CssBaseline />
      <Drawer
        className={classes.drawer}
        variant="permanent"
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <Toolbar />
        <div className={classes.drawerContainer}>
          <List>
            {itemsList.map((item, index) => {
              const { text, icon, onClick } = item;
              return (
                <ListItem button key={text} onClick={onClick}>
                  {icon && <ListItemIcon>{icon}</ListItemIcon>}
                  <ListItemText primary={text} />
                </ListItem>
              );
            })}
        </List>
          <Divider />
          <List>
            {['Documentation', 'About'].map((text, index) => (
              <ListItem button key={text}>
                <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
                <ListItemText primary={text} />
              </ListItem>
            ))}
          </List>
        </div>
      </Drawer>
      <main className={classes.content}>
        <Toolbar />
        < Switch>
        <Route exact path="/dashboard/:id/" component={() => <Content loading={loading} GuildName={data["GuildName"]} GuildMembers={data["Members"]} GuildChannels={data["NumberOfChannels"]}/>} />

        <Route path="/dashboard/:id/plugins" component={() => <Plugins url={props.match.params.id} Roles={data["Roles"]} Channels={data["Channels"]} Categories={data["Categories"]} VerifyEnabled={data["VerifyEnabled"]} TicketsEnabled={data["TicketsEnabled"]} ShowcaseEnabled={data["ShowcaseEnabled"]} ActiveInactiveEnabled={data["ActiveInactiveEnabled"]}/>}/>
        <Route path="/dashboard/:id/tickets" component={() => <Tickets url={props.match.params.id} OpenedTickets={data["OpenedTickets"]} TicketsCategories={data["TicketsCategories"]} Roles={data["Roles"]} Channels={data["Channels"]} Categories={data["Categories"]} TicketsEnabled={data["TicketsEnabled"]} TicketsEnabled={data["TicketsEnabled"]} ShowcaseEnabled={data["ShowcaseEnabled"]} ActiveInactiveEnabled={data["ActiveInactiveEnabled"]}/>}/>
        </ Switch>

      </main>
    </div>
    </ThemeProvider>
  );
}