import {React, useState} from "react";
import ReactDOM from "react-dom";
import { useForm, ErrorMessage, Controller } from "react-hook-form";
import {
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  FormHelperText,
  Button,
  Grid,
  TextField
} from "@material-ui/core";
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import SaveIcon from '@material-ui/icons/Save';
import {makeStyles, withStyles} from '@material-ui/core/styles';

export default function FormVerification(props) {


  const greenTheme = createMuiTheme({ palette: { 
    primary: {
        main: "#00e676",
        contrastText: "#fff"
    } }})

    const useStyles = makeStyles((theme) =>({
        root:{
            margin: theme.spacing(9),
            textAlign: "center",
        },
        submit:{
            marginTop: 30
        },
        selectField:{
            margin: theme.spacing(1),
            width: 380,
            minWidth: 120,
            "& .MuiOutlinedInput-input": {
              color: "white"
            },
            "& .MuiInputLabel-root": {
              color: "white"
            },
            "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
              borderColor: "white"
            },
            "&:hover .MuiOutlinedInput-input": {
              color: "white"
            },
            "&:hover .MuiInputLabel-root": {
              color: "white"
            },
            "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
              borderColor: "white"
            },
            "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-input": {
              color: "white"
            },
            "& .MuiInputLabel-root.Mui-focused": {
              color: "white"
            },
            "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
              borderColor: "white"
            },
            '& label.Mui-focused': {
                color: 'white',
            },
            '& .MuiInput-underline:after': {
            borderBottomColor: 'green',
            },
            '& .MuiOutlinedInput-root': {
                '& fieldset': {
                borderColor: 'white',
                },
                '&:hover fieldset': {
                borderColor: 'white',
                },
                '&.Mui-focused fieldset': {
                borderColor: 'white',
                },
            }  
        }
}));
    const CssTextField = withStyles({
        root: {
          '& label.Mui-focused': {
            color: 'white',
          },
          '& .MuiInput-underline:after': {
            borderBottomColor: 'green',
          },
          '& .MuiOutlinedInput-root': {
            '& fieldset': {
              borderColor: 'white',
            },
            '&:hover fieldset': {
              borderColor: 'white',
            },
            '&.Mui-focused fieldset': {
              borderColor: 'white',
            },
          },
        },
      })(TextField);


    const classes = useStyles();

    const {  register, handleSubmit, errors, control } = useForm();
    const [information, setInformation] = useState({});
    const [ buttonType, setButtonType] = useState();

    const onSubmitCurrent = handleSubmit(data => {
        data.formType = 'formCurrent'
        console.log(data)   
        fetch(`/api/setverify/${props.url}`,{
        method: "POST",
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data)
    }).then(response => response.json())
    .then(data => props.onChange(data.response)
    )
    });

    const onSubmitNew = handleSubmit(data => {
      data.formType = 'formNew'
      console.log(data)   
      fetch(`/api/setverify/${props.url}`,{
      method: "POST",
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data)
  }).then(response => response.json())
  .then(data => props.onChange(data.response)
  )
  });

    const menuOptionsChannels = [];
    const menuOptionsRoles = [];

    const test =  {"743211086458650687":"general","744273735149813882":"help","744275473881890896":"verification","744318477095010395":"developer_channel","744319814100582590":"closed-questions","744340921608568962":"rules","744541100127027288":"report-bugs","749240633205588038":"information","779647561388130314":"votes"}

    const getChannels = () =>{
        for(const Channel in props.Channels){
            menuOptionsChannels.push(<MenuItem key={Channel} value={String(Channel)}>{String(props.Channels[String(Channel)])}</MenuItem>);
        }
    }
    const getRoles = () =>{
        for(const Role in props.Roles){
            menuOptionsRoles.push(<MenuItem key={Role} value={String(Role)}>{String(props.Roles[String(Role)])}</MenuItem>);
        }
    }

    getChannels();
    getRoles();

    return (
        <Grid>
        <div className={classes.root}>
        <form>
            <div>
            <Grid item xs={50}>
            <CssTextField
                fullWidth={true}
                id="outlined-multiline-static"
                label="Multiline"
                multiline
                rows={8}
                defaultValue="Content of the verification message"
                variant="outlined"
                inputRef={register}
                name="Message"
                color= "secondary"

                />
            </Grid>
            <Grid item xs={12}>
            <FormControl className={classes.selectField}
                style={{ minWidth: 300 }}
                error={Boolean(errors.channel)}
            >
                <InputLabel id="channel">
                    Channel to send message
                </InputLabel>
                <Controller 
                as={
                    <Select>
                        {menuOptionsChannels}
                    </Select>
                }
                name="channel"
                rules={{ required: "This is required" }}
                control={control}
                defaultValue=""
                color="secondary"
                />
                <FormHelperText>
                {errors.channel && errors.channel.message}
                </FormHelperText>
            </FormControl>
            </Grid>
            <Grid item xs={12}>
            <FormControl className={classes.selectField}
                style={{ minWidth: 300 }}
                error={Boolean(errors.role)}
            >
                <InputLabel id="roleSelect">
                    Role of verified members
                </InputLabel>
                <Controller 
                as={
                    <Select>
                        {menuOptionsRoles}
                    </Select>
                }
                name="role"
                rules={{ required: "This is required" }}
                control={control}
                defaultValue=""
                variant="standard"

                />
                <FormHelperText>
                {errors.role && errors.role.message}
                </FormHelperText>
            </FormControl>
            </Grid>
            </div>
        <Grid item xs={12}>
        <ThemeProvider theme={greenTheme}>
                <Button
                startIcon={<SaveIcon />}
                type="submit"
                fullWidth
                variant="outlined"
                color="primary"
                className={classes.submit}
                onClick={onSubmitCurrent}
                >

                current configuration
                </Button>
            </ThemeProvider>
        </Grid>

        <Grid item xs={12}>

            <ThemeProvider theme={greenTheme}>
                <Button
                startIcon={<SaveIcon />}
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                onClick={onSubmitNew}
                >
                New configuration
                </Button>
            </ThemeProvider>
        </Grid>
        </form>
        </div>

        </Grid>
        
    );
}