import {React, useState, useEffect} from 'react'
import Grid from '@material-ui/core/Grid';
import {Card, Typography, CardContent, CardActions, CardActionArea, Dialog, DialogTitle, DialogContent, List } from '@material-ui/core';
import {makeStyles, withStyles} from '@material-ui/core/styles';
import Switch from '@material-ui/core/Switch';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';
import Ticket from '../components/Ticket';
import Content from '../components/Content';

function CardCategory(props){

  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };
  
  const handleClose = () =>{

    setOpen(false);
  }



  const useStyles = makeStyles((theme) =>({
    root:{
      textAlign: "center",
      background: props.color
    },
    text:{
      fontSize: 14,
      color: props.font
    },
    title:{
      fontWeight: "bold",
      fontSize: 22,
      color: props.font
    }

  }));

  const styles = (theme) => ({
    root: {
      margin: 0,
      padding: theme.spacing(2),
    },
    closeButton: {
      position: 'absolute',
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.grey[500],
    },
  });


  const clases=useStyles();

  const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
      <MuiDialogTitle disableTypography className={classes.root} {...other}>
        <Typography variant="h6">{children}</Typography>
        {onClose ? (
          <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
            <CloseIcon />
          </IconButton>
        ) : null}
      </MuiDialogTitle>
    );
  });
  
  const DialogContent = withStyles((theme) => ({
    root: {
      padding: theme.spacing(2),
    },
  }))(MuiDialogContent);




  return(
    <Card className={clases.root}>
    <CardActionArea onClick={handleOpen}>
      <CardContent>
        {props.icon}
        <Typography className={clases.title}>
          {props.title}
        </Typography>
        <Typography className={clases.text}>
          Click here to view tickets of {props.title}
        </Typography>
      </CardContent>
    </CardActionArea>
    <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open} fullWidth={true} maxWidth = {'md'}>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
        <Grid container direction="row" alignItems="center">
          <Grid item>
          </Grid>
          <Grid item>
            {props.title}
          </Grid>
        </Grid>
        </DialogTitle>
        <DialogContent dividers>
          <Content icon={props.icon} url={props.url}/>
        </DialogContent>
      </Dialog>
    </Card>



  );
}

export default CardCategory;
