import {React, useState, useEffect} from 'react';
import Grid from '@material-ui/core/Grid';
import {makeStyles, withStyles} from '@material-ui/core/styles';
import "fontsource-roboto";
import '../assets/Dashboard.css'
import Category from '../components/Category';
import CardCategory from '../components/CardCategory';
import FormAddCategory from './Forms/FormAddCategory';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import {Card, Typography, Dialog} from '@material-ui/core';
import { useForm} from "react-hook-form";
import {
    Button,
    TextField,
    Paper,
    List
  } from "@material-ui/core";
import AddCircleIcon from '@material-ui/icons/AddCircle';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css'
import './css/Toas.css';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';
import 'emoji-mart/css/emoji-mart.css'
import { Picker } from 'emoji-mart'
import DeleteRoundedIcon from '@material-ui/icons/DeleteRounded';

const useStyles = makeStyles((theme) => ({
  root:{
    flexGrow: 1,
  },
  icons:{
    color: "white"
  },
  containerGraphics:{
    marginTop: '40px'
  },
  containerTable:{
    marginTop: '40px'
  },
  section:{
    height: "100%",
  },
  categories:{
    marginTop: 5,
  },
  demo: {
    backgroundColor: theme.palette.background.paper,
    borderRadius: 5,
    shadowColor: "black"
  },
  button:{
    width: "80%",
    alignItems: "center",
    justifyContent: "center",
    marginLeft: "10%",
    margin: 30,
  },
  text:{
    fontSize: 30,
    marginBottom: 10,
    width: "80%",
    alignItems: "center",
    justifyContent: "center",
    flexGrow: 1,
    marginLeft: "1%",

    },
  emojiSelector:{
    background: "#2C2F33",
    color: "blue",
    backgroundColor: "#454242"

  }




}));

export default function Categories(props){

  const {  register, handleSubmit, errors, control } = useForm();


  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
    };
  
  const handleClose = () =>{

    setOpen(false);
  }


  const classes= useStyles();

  const greenTheme = createMuiTheme({ palette: { 
    primary: {
        main: "#00e676",
        contrastText: "#fff"
    } }})


    const[data, setData] = useState([])
    const[update, setUpdate] = useState([])


    useEffect(() => {
  
      const getData = async () =>{
        const response = await fetch(`/api/dashboard/${props.url}/tickets`)
        const response_json = await response.json()
        setData(response_json)
        console.log(response_json)
      }
      getData()
    }, [update, setUpdate])


  

  
  const styles = (theme) => ({
    root: {
      margin: 0,
      padding: theme.spacing(2),
    },
    closeButton: {
      position: 'absolute',
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.grey[500],
    },
  });

  const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
      <MuiDialogTitle disableTypography className={classes.root} {...other}>
        <Typography variant="h6">{children}</Typography>
        {onClose ? (
          <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
            <CloseIcon />
          </IconButton>
        ) : null}
      </MuiDialogTitle>
    );
  });
  
  const DialogContent = withStyles((theme) => ({
    root: {
      padding: theme.spacing(2),
    },
  }))(MuiDialogContent);


  const [chosenEmoji, setChosenEmoji] = useState(null);
  const [visiblePicker, setVisiblePicker] = useState(false);

  const onEmojiClick = (emojiObject) => {
    setChosenEmoji(emojiObject["native"]);
    showPicker()
  };

  const showPicker = () => {
    setVisiblePicker(!visiblePicker)
  }

  const removeEmoji = () => {
    setChosenEmoji(false)
  }
  const handleNotification = (value) =>{
    Update()

    handleClose();
    if(value == "error"){
      toast.error(" Error!", {
        position: "bottom-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
    });
    }
    else{
      Update()
      setChosenEmoji(false)
      toast.success(" Saved!", {
        position: "bottom-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
    });
    }
  }

  const Update = () => {
    console.log("ACTIVATED")
    setUpdate(["test"])
  }
  return(
    <div className={classes.root}>
      <Grid   container spacing={4}>

      <Grid item xs={12} sm={12} md={12} lg={6}>
      <Paper elevation={2}>
        <List>
        {data.map((TicketCategory) => <Category onChange={Update} url={props.url} name={TicketCategory["name"]} icon={TicketCategory["emoji"]}/>
        )}
        </List>
            <ThemeProvider theme={greenTheme}>
          <Button
              startIcon={<AddCircleIcon />}
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.button}
              onClick={handleOpen}
              >
              Add New Category
          </Button>
        </ThemeProvider>     
        </Paper>  
        </Grid>
        <Grid item xs={12} sm={10} md={9} lg={6}>
        <Grid container spacing={4}>
        {data.map((TicketCategory) => <Grid item xs={12} sm={10} md={9} lg={3}>
              <CardCategory OpenedTickets={props.OpenedTickets} title={TicketCategory["name"]} url={props.url} icon={TicketCategory["emoji"]}/>
          </Grid>
           
        )}        
        </Grid>
        </Grid>
        <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open} fullWidth={true} maxWidth = {'md'}>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
        <Grid container direction="row" alignItems="center">
          <Grid item>
          </Grid>
          <Grid item>
            Add New Category
          </Grid>
        </Grid>
        </DialogTitle>
        <DialogContent dividers>

        {chosenEmoji && (
        <div>
        <Typography className={classes.text}>
        <IconButton className={classes.deleteButton}>
                      <DeleteRoundedIcon onClick={removeEmoji}/>
        </IconButton>
             Emoji {chosenEmoji}

        </Typography>
        <FormAddCategory url={props.url} Roles={props.Roles} Emoji={chosenEmoji} onChange={(value) => handleNotification(value)}/>
        </div>
        )}
        {!chosenEmoji && (
        <ThemeProvider theme={greenTheme}>
          <Button
              startIcon={<AddCircleIcon />}
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.button}
              onClick={showPicker}
              >
              Add Emoji
          </Button>
        </ThemeProvider>
        )}
        <span>
        {visiblePicker && (
        <Picker onSelect={(emoji) => {onEmojiClick(emoji)}} theme="dark" set="twitter" title="Choose a react icon" showSkinTones={false}/>
        )}
        </span>
        </DialogContent>
      </Dialog>
      </Grid>









    </div>
  );

}
