import React from 'react'
import Grid from '@material-ui/core/Grid';
import {Table, TableBody, TableCell, TableContainer, TableHead, TableRow} from '@material-ui/core';
import {withStyles} from '@material-ui/core/styles';

const StyledTableCell = withStyles(() =>({
  head:{
    color: "white",
    background: "rgba(66, 66, 66, 1)",
    textAlign: "center",
  },
  body:{
    fontSize: 14,
  },
}))(TableCell);

export default function TableMaterials(props){


  return(
    <TableContainer>
    <Table>
      <TableHead>
        <TableRow>
            <StyledTableCell>News</StyledTableCell>
            <StyledTableCell>Date</StyledTableCell>
            <StyledTableCell>Changes</StyledTableCell>

        </TableRow>
      </TableHead>

      <TableBody>
        {props.data.map(element=>(
          <TableRow key={element.id}>
            <TableCell>{element.new}</TableCell>
            <TableCell align="center">{element.date}</TableCell>
            <TableCell align="center">{element.changes}</TableCell>
          </TableRow>
        ))}
      </TableBody>
      </Table>
    </TableContainer>


  );
}
