import { React, useCallback, useEffect, useState } from 'react';
import Grid from '@material-ui/core/Grid';
import {Card, Typography, CardContent, CardActions, CardActionArea, LinearProgress, FormControl, InputLabel, Input, FormHelperText } from '@material-ui/core';
import {makeStyles, withStyles} from '@material-ui/core/styles';
import Switch from '@material-ui/core/Switch';
import { useIosSwitchStyles } from '@mui-treasury/styles/switch/ios';
import SettingsRoundedIcon from '@material-ui/icons/SettingsRounded';
import IconButton from '@material-ui/core/IconButton';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import CloseIcon from '@material-ui/icons/Close';
import FormVerification from './Forms/FormVerification';
import FormShowcase from './Forms/FormShowcase';
import FormActiveInactive from './Forms/FormActiveInactive';

import { toast } from 'react-toastify';
import { css } from "glamor";
import 'react-toastify/dist/ReactToastify.css'
import './css/Toas.css';
import FormTickets from './Forms/FormTickets';
import FormTranscript from './Forms/FormTranscript';

toast.configure()
function CardPlugin(props){
  const [toggled, setToggled] = useState(!props.checked);
  const switchStyles = useIosSwitchStyles ();


  const useStyles = makeStyles((theme) =>({
    root:{
      textAlign: "center",
      background: props.color
    },
    text:{
      fontSize: 18,
      color: props.font
    },
    title:{
      fontWeight: "bold",
      fontSize: 22,
      color: props.font
    },
    switcher:{
    },
    formControl: {
      margin: theme.spacing(1),
      width: 380,
      minWidth: 120,
    
      
    },


    paper: {
      marginTop: theme.spacing(8),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.secondary.main,
    },
    form: {
      width: '100%', // Fix IE 11 issue.
      marginTop: theme.spacing(1),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    },
    notchedOutline: {
      borderWidth: "1px",
      borderColor: "yellow !important"
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: 'center',
      color: theme.palette.text.secondary,
    },
    icon:{
      margin: 7,
      marginTop: 15
    }

  }));

/* DIALOG STYLES START*/

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

  const [open, setOpen] = useState(false);
  const clases = useStyles();
  const handleOpen = () => {
    setOpen(true);
  };
  
  const handleClose = () =>{

    setOpen(false);
  }


  /* DIALOG STYLES FINAL*/

  const ButtonStyle = (() => ({
    root:{
      backgroundColor: "green"
    }


  }));


  const handleNotification = (value) =>{

    handleClose();
    if(value == "error"){
      toast.error(" Error!", {
        position: "bottom-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
    });
    }
    else{
      toast.success(" Saved!", {
        position: "bottom-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
    });
    }
  }

  const btstyle = ButtonStyle(); 
  const onSubmit = data => {   
    setToggled(data)
    localStorage.setItem(props.formSelect, JSON.stringify(data))
    fetch(`/api/${props.formSelect}/${props.url}`,{
    method: "POST",
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(!data)
  }).then(response => response.json())
  .then(data => handleNotification(data.response)
  )
  };

  const selectForm = () => {
    if(props.formSelect == "setverify"){
      return <FormVerification Roles={props.Roles} Channels={props.Channels} onChange={(value) => handleNotification(value)} url={props.url}/>
    }
    if(props.formSelect == "setshowcase"){
      return <FormShowcase Channels={props.Channels} onChange={(value) => handleNotification(value)} url={props.url}/>
    }
    if(props.formSelect == "setactiveinactive"){
      return <FormActiveInactive Categories={props.Categories} onChange={(value) => handleNotification(value)} url={props.url}/>
    }
    if(props.formSelect == "settickets"){
      return <FormTickets Channels={props.Channels} onChange={(value) => handleNotification(value)} url={props.url}/>
    }
    if(props.formSelect == "settranscript"){
      return <FormTranscript Channels={props.Channels} onChange={(value) => handleNotification(value)} url={props.url}/>
    }
  }

  const selectedForm = selectForm()
  return(
    

    <Card className={clases.root}>

      <CardContent>
        {props.icon}
        <Typography className={clases.title}>
          {props.title}
        </Typography>
        <Typography className={clases.text}>
          {props.text}
        </Typography>

        <IconButton aria-label="play/pause" onClick={handleOpen}>
          <SettingsRoundedIcon/>
        </IconButton>
        <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
        <Grid container direction="row" alignItems="center">
          <Grid item>
            <SettingsRoundedIcon className={clases.icon}/>
          </Grid>
          <Grid item>
            {props.title}
          </Grid>
        </Grid>
        </DialogTitle>
        <DialogContent dividers>
          {selectedForm}
        </DialogContent>
      </Dialog>
          
        <Switch
        classes={switchStyles}
        checked={!toggled}
        onChange={data => onSubmit(!data.target.checked)}
        />
        

      </CardContent>

    </Card>



  );
}

export default CardPlugin;
