import {React, useState, useEffect} from 'react'
import Grid from '@material-ui/core/Grid';
import {Card, Typography, CardContent, CardActions, CardActionArea, Dialog, DialogTitle, DialogContent, List } from '@material-ui/core';
import {makeStyles, withStyles} from '@material-ui/core/styles';
import Switch from '@material-ui/core/Switch';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';
import Ticket from '../components/Ticket';

function Content(props){

  

  const[data, setData] = useState([])
  const[update, setUpdate] = useState([])
  const x = []

  useEffect(() => {

    const getData = async () =>{
      const response = await fetch(`/api/dashboard/${props.url}/category_tickets`)
      const response_json = await response.json()
      setData(response_json)
      console.log(response_json)
    }
    getData()
    
  }, [update, setUpdate])


  data.map((TicketCategory)=>{
    if(props.icon == TicketCategory.category){
      x.push(<Ticket title={TicketCategory.title} username={TicketCategory.username} ticketid={TicketCategory.ticketid} url={props.url}/>)

    }
  } )

  return(
    <div>
      {x}
    </div>)
}

export default Content;
