const useStyles = makeStyles(() => ({
  root:{
    flexGrow: 1,
    display: 'flex',
    marginTop: 7,
    height: "50px",
  },
  icons:{
    color: "white"
  },
  containerGraphics:{
    marginTop: '40px'
  },
  containerTable:{
    marginTop: '40px'
  },
  section:{
    height: "100%",
  },
  categories:{
    marginTop: 7,
  },
  button:{
    flexGrow: 1,



  },
  details: {
    display: 'flex',
    flexDirection: 'column',
  },




}));

function generate(element) {
  return [0, 1, 2].map((value) =>
    cloneElement(element, {
      key: value,
    }),
  );
}
export default function Ticket(props){
    const {  register, handleSubmit, errors, control } = useForm();
    const [dense, setDense] = useState(false);
    const [secondary, setSecondary] = useState(false);


  const classes= useStyles();

  const redTheme = createMuiTheme({ palette: { 
    primary: {
        main: "#ab003c",
        contrastText: "#fff"
    } }})

    const handleNotification = (value) =>{

      if(value == "error"){
        toast.error(" Error!", {
          position: "bottom-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
      });
      }
      else{
        toast.success(" Saved!", {
          position: "bottom-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
      });
      }
    }
    const [showResults, setShowResults] = useState(true)
  
    const onSubmitNew = handleSubmit(data => {
      data.formType = 'formNew'
      data.ticketid = props.ticketid
      console.log(data)   
      fetch(`/api/closeticket/${props.url}`,{
      method: "POST",
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data)
    }).then(response => response.json())
    .then(data => {
      handleNotification(data.response)
      setShowResults(false)
    }
  
    )
    });


  return(
    <Grid>
    <Grid item xs={12} sm={6} md={4} lg={12}>
      {showResults && (
    <List>

                <ListItem>
                  <ListItemAvatar>
                    <Avatar>
                      <ChatRoundedIcon />
                    </Avatar>
                  </ListItemAvatar>
                  <ListItemText primary={props.title} />
                  <ListItemText>
                    By {props.username}
                  </ListItemText>
                  <ListItemSecondaryAction>
                    <IconButton edge="end" aria-label="delete">
                      <CancelRoundedIcon onClick={onSubmitNew} />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
                </List>

)}
    </Grid>
    </Grid>


  );

}