import React from 'react'
import Grid from '@material-ui/core/Grid';
import {Card, Typography, CardContent, CardActions, CardMedia} from '@material-ui/core';
import {makeStyles} from '@material-ui/core/styles';
import Switch from '@material-ui/core/Switch';
import Button from '@material-ui/core/Button';
import { useFirebaseBtnStyles } from '@mui-treasury/styles/button/firebase';
import { usePushingGutterStyles } from '@mui-treasury/styles/gutter/pushing';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';

function ServerSelector(props){

    const useStyles = makeStyles((theme) =>({
      root:{
        display: 'flex',
        flexGrow: 1,
        marginTop: 21,
        height: "100px",

        textAlign: "center",
        background: props.backgroundColor
      },
      text:{
        display: 'flex',
        flexGrow: 1,
        
        marginLeft: 15
      },
      title:{
        marginTop: 15,
        display: 'flex',
        flexGrow: 1,

        textAlign: "left",
        fontWeight: "bold",
        fontSize: 22,
        color: props.font
      },
      cover: {
        width: 151,
      },
      button:{
        display: 'flex',
        flexGrow: 1,
        textAlign: "right",
        alignItems: 'center',
        paddingLeft: theme.spacing(1),
        paddingBottom: theme.spacing(1),
        margintLeft: 15


      },
      media: {
          
        marginTop: "auto",
        marginBottom: "auto",
        marginLeft: "1.5%",
        borderRadius: "50%",
        height: "64px",
        width: "64px"

    },
    details: {
        display: 'flex',
      },
  
    }));
  
    const clases=useStyles();
    const styles = useFirebaseBtnStyles();
    const gutterStyles = usePushingGutterStyles();

    const greenTheme = createMuiTheme({ palette: { 
      primary: {
          main: "#00e676",
          contrastText: "#fff"
      } }})

    return(
      <Card className={clases.root}>
                  <CardMedia
        className={clases.media}
        image= {props.image}
        title="Server Image"
      /> 
      <div className={clases.details}>
        <CardContent>
          <Typography className={clases.title}>
            {props.title}
          </Typography>

        </CardContent>
      </div>
      <CardActions >
      <ThemeProvider theme={greenTheme}>
        {props.linkto &&
        <Link style={{ textDecoration: 'none', color:"white" }} to={props.linkto}>
        <Button component="a" href={props.externallinkto} size="large" className={clases.button} classes={styles} variant={props.buttonVariant} color="primary">
            <div className={clases.text}>
            {props.text}
            </div>
          </Button>
          </Link>
        }
        {!props.linkto &&
        <Button component="a" href={props.externallinkto} size="large" className={clases.button} classes={styles} variant={props.buttonVariant} color="primary">
          <div className={clases.text}>
            {props.text}
          </div>
        </Button>}
        
      </ThemeProvider>
      </CardActions>

 
      </Card>
    
  
  
    );
  }
  
  export default ServerSelector;
  