import React from 'react';
import Grid from '@material-ui/core/Grid';
import {makeStyles} from '@material-ui/core/styles';
import "fontsource-roboto";
import '../assets/Dashboard.css'
import PeopleAltIcon from '@material-ui/icons/PeopleAlt';
import ChatIcon from '@material-ui/icons/Chat';
import QuestionAnswerIcon from '@material-ui/icons/QuestionAnswer';
import CardHeader from '../components/CardsHeader';
import Graphics from '../components/Graphics';
import TableMaterials from '../components/TableMaterials';
import { DisappearedLoading } from 'react-loadingg';


const useStyles = makeStyles(() => ({
  root:{
    flexGrow: 1
  },
  icons:{
    color: "white"
  },
  containerGraphics:{
    marginTop: '40px'
  },
  containerTable:{
    marginTop: '40px'
  }




}));

const data = [
    {
      id:1,
      new:
        "New tickets features check it now!",
      date: "6 de sep. 2020",
      changes: 32,
    },
    {
      id:2,
        new:
          "New Verify feature!",
        date: "5 de sep. 2020",
        changes: 31,
      },
      {
      id:3,
        new:
          "SpartBot is now available for everyone",
        date: "4 de sep. 2020",
        changes: 21,
      },
  ];

function Content(props){
  const clases= useStyles();
  const test = {"Channels":14,"GuildName":"SpartBot Support","Members":11}
  return(

    <div className={clases.root}>
      {props.loading && <DisappearedLoading size="large" speed={.5} color="#587dd5" style={{marginLeft: "50%", marginButtom: "50%", marginTop: "20%"}}/>}
      {!props.loading &&
      <Grid container spacing={3}>

        <Grid item xs={12} sm={4} md={4} lg={4} lg={4}>
          <CardHeader icon={<ChatIcon className={clases.root}/>} title={props.GuildName} text="Discord Server" color="#3D3A3A" font="white"/>
        </Grid>

        <Grid item xs={12} sm={4} md={4} lg={4} lg={4}>
          <CardHeader icon={<PeopleAltIcon className={clases.root}/>} title={props.GuildMembers} text="Users" color="#3D3A3A" font="white"/>
        </Grid>

        <Grid item xs={12} sm={4} md={4} lg={4} lg={4}>
          <CardHeader icon={<QuestionAnswerIcon className={clases.root}/>} title={props.GuildChannels} text="Channels" color="#3D3A3A" font="white"/>
        </Grid>



        <Grid item xs={12} sm={12} md={12} lg={12} lg={12} className={clases.containerTable}>
          < TableMaterials data={data}/>
        </Grid>

      </Grid>
      }



    </div>

  );
}

export default Content;
