import {React, useState} from "react";
import ReactDOM from "react-dom";
import { useForm, ErrorMessage, Controller } from "react-hook-form";
import {
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  FormHelperText,
  Button,
  Grid,
  TextField
} from "@material-ui/core";
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import SaveIcon from '@material-ui/icons/Save';
import {makeStyles, withStyles} from '@material-ui/core/styles';

export default function FormAddCategory(props) {


  const greenTheme = createMuiTheme({ palette: { 
    primary: {
        main: "#00e676",
        contrastText: "white",
    } }})

    const useStyles = makeStyles((theme) =>({
        root:{
            margin: theme.spacing(5),
            textAlign: "center",
        },
        submit:{
            marginTop: 50
        },
        selectField:{
            margin: theme.spacing(1),
            width: 380,
            minWidth: 120,
            "& .MuiOutlinedInput-input": {
              color: "white"
            },
            "& .MuiInputLabel-root": {
              color: "white"
            },
            "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
              borderColor: "white"
            },
            "&:hover .MuiOutlinedInput-input": {
              color: "white"
            },
            "&:hover .MuiInputLabel-root": {
              color: "white"
            },
            "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
              borderColor: "white"
            },
            "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-input": {
              color: "white"
            },
            "& .MuiInputLabel-root.Mui-focused": {
              color: "white"
            },
            "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
              borderColor: "white"
            },
            '& label.Mui-focused': {
                color: 'white',
            },
            '& .MuiInput-underline:after': {
            borderBottomColor: 'green',
            },
            '& .MuiOutlinedInput-root': {
                '& fieldset': {
                borderColor: 'white',
                },
                '&:hover fieldset': {
                borderColor: 'white',
                },
                '&.Mui-focused fieldset': {
                borderColor: 'white',
                },
            }  
        }
}));

const CssTextField = withStyles({
  root: {
    '& label.Mui-focused': {
      color: 'green',
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: 'green',
    },
    '& .MuiOutlinedInput-root': {
      '& fieldset': {
        borderColor: 'red',
      },
      '&:hover fieldset': {
        borderColor: 'yellow',
      },
      '&.Mui-focused fieldset': {
        borderColor: 'green',
      },
    },
  },
})(TextField);


    const classes = useStyles();

    const {  register, handleSubmit, errors, control } = useForm();


    const onSubmitNew = handleSubmit(data => {
      data.formType = 'formNew'
      data.emoji= props.Emoji
      console.log(data)   
      fetch(`/api/addcategory/${props.url}`,{
      method: "POST",
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data)
  }).then(response => response.json())
  .then(data => props.onChange(data.response)
  )
  });

    const menuOptionsRoles = [];

    const test =  {"743211086458650687":"general","744273735149813882":"help","744275473881890896":"verification","744318477095010395":"developer_channel","744319814100582590":"closed-questions","744340921608568962":"rules","744541100127027288":"report-bugs","749240633205588038":"information","779647561388130314":"votes"}


    const getRoles = () =>{
        for(const Role in props.Roles){
            menuOptionsRoles.push(<MenuItem key={Role} value={String(Role)}>{String(props.Roles[String(Role)])}</MenuItem>);
        }
    }

    getRoles();

    return (
        <Grid>
        <div className={classes.root}>
        <form>
            <div>
            <Grid item xs={50}>
            <CssTextField
                fullWidth={true}
                id="outlined-multiline-static"
                label="Category Name"

                rows={8}
                defaultValue="Category Name"
                inputRef={register}
                name="categoryName"

                />
            </Grid>
            <Grid item xs={12}>
            <FormControl className={classes.selectField}
                style={{ minWidth: 300, width: "100%", marginTop: 20 }}
                error={Boolean(errors.role)}
            >
                <InputLabel id="roleSelect">
                    Role that can see this category
                </InputLabel>
                <Controller 
                as={
                    <Select>
                        {menuOptionsRoles}
                    </Select>
                }
                name="role"
                rules={{ required: "This is required" }}
                control={control}
                defaultValue=""
                variant="standard"

                />
                <FormHelperText>
                {errors.role && errors.role.message}
                </FormHelperText>
            </FormControl>
            </Grid>
            </div>

        <Grid item xs={12}>

            <ThemeProvider theme={greenTheme}>
                <Button
                startIcon={<SaveIcon />}
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                onClick={onSubmitNew}
                >
                Add Category
                </Button>
            </ThemeProvider>
        </Grid>
        </form>
        </div>

        </Grid>
        
    );
}