import React from 'react';
import {Line} from 'react-chartjs-2';
import "../assets/Graphics.css";

function Graphics(props){
  const data={
    labels:["5 days", "4 days", "3 days", "2 days", "1 day"],
    datasets:[{
      label: props.name,
      fill:false,
      backgroundColor:"rgba(63, 63, 90, 1)",
      borderColor:"rgba(88, 125, 213, 1)",
      pointBorderColor:"rgba(88, 125, 213, 1)",
      pointBorderWidth:1,
      pointHoverRadius:5,
      pointHoverBackgroundColor:"rgba(88, 125, 213, 1)",
      pointHoverBorderColor:"rgba(88, 125, 213, 1)",
      pointRadius:1,
      pointHitRadius: 10,
      data: props.data
      }
    ]
  }


  return(
    <div className="containerGraphics">
      <Line data={data} />
    </div>
  );



}

export default Graphics;
