import {React, useState, useEffect} from 'react';
import Grid from '@material-ui/core/Grid';
import {makeStyles} from '@material-ui/core/styles';
import "fontsource-roboto";
import '../assets/Dashboard.css'
import CardPlugin from '../components/CardsPlugins';
import Categories from '../components/Categories';
import EmailIcon from '@material-ui/icons/Email';
import ReplyAllRoundedIcon from '@material-ui/icons/ReplyAllRounded';
import { DisappearedLoading } from 'react-loadingg';

const useStyles = makeStyles(() => ({
  root:{
    flexGrow: 1
  },
  icons:{
    color: "white"
  },
  containerGraphics:{
    marginTop: '30px'
  },
  containerTable:{
    marginTop: '30px'
  },
  section:{
    height: "90px",
  }




}));


export default function Tickets(props){


  const[data, setData] = useState([])
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const getData = async () =>{
      setLoading(true);
      const response = await fetch(`/api/dashboard/${props.url}/plugins`)
      const response_json = await response.json()
      setData(response_json)
      console.log(response_json)
      
      setLoading(false)

    }
    getData()
  }, [])



  const classes= useStyles();



  return(
    <div className={classes.root}>
      {loading && <DisappearedLoading size="large" speed={.5} color="#587dd5" style={{marginLeft: "50%", marginButtom: "50%", marginTop: "20%"}}/>}
      {!loading &&
      <Grid   container

        spacing={3}>
        <Grid item xs={12} sm={12} md={12} lg={6}>
            <CardPlugin icon={<EmailIcon className={classes.root}/>} formSelect={"settickets"} Channels={props.Channels} url={props.url} checked={parseInt(data["TicketsEnabled"])} title="Tickets" text="Set-up an advanced ticket system to your server!" color="#3D3A3A" font="white"/>
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={6}>
          <CardPlugin icon={<ReplyAllRoundedIcon className={classes.root}/>} formSelect={"settranscript"} Channels={props.Channels} url={props.url} checked={parseInt(data["VerifyEnabled"])} title="Transcript Tickets" text="Transcript your tickets and send it to a Discord channel" color="#3D3A3A" font="white"/>
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={12}>
          <Categories url={props.url} Roles={props.Roles} OpenedTickets={props.OpenedTickets} TicketsCategories={props.TicketsCategories}/>
        </Grid>




      </Grid>

}


    </div>
  );

}
